import uniRate from "@/components/uni-rate/uni-rate.vue"

export default {
	components: {
		uniRate
	},
	data() {
		return {
			clazzName: '',
			teacher: '',
			time: '',
			id: '',
			show: true,
			jxyf:'',
			jxyfss:false,
			jjnxss:false,
			jjnx:'',
			content:'',
			reviewReplyContent:'',
		};
	},
	onLoad(o){
		this.id = o.id;
		this.show = true
	},
	onShow(){
		this.getinfo();
	},
	methods: {
		jjnxs(e){
			this.jjnx = e;
		},
		jxyfs(e){
			this.jxyf = e;
		},
		getinfo(){
			uni.request({
				url: this.$api+'/weixin/shishenxin/assessmentliessoninfo',
				data: this.getmd5({
					lessonId:this.id
				}),
				header: {
					'content-type': 'application/json'
				},
				method: "POST",
				success: (res) => {
					if (res.data.code != 200) {
						uni.showToast({
						icon: 'none',
						title: res.data.msg,
						duration: 2000
						});
					} else {
						var data = res.data.data;
						this.clazzName = data.clasName;
						this.teacher = data.teacherName;
						this.time = data.classDate+' '+data.classTime+'-'+data.leaveTime;
						this.jxyf = data.jxyf;
						this.jjnx = data.jjnx;
						this.reviewReplyContent = data.reviewReplyContent;
						if(data.jxyf || data.jjnx){
							this.show = false;
							this.jxyfss = true;
							this.jjnxss = true;
						}
					}
				},
				fail: () => {
					uni.hideLoading();
					uni.showToast({
					icon: 'none',
					title: '获取失败',
					duration: 2000
					});
				}
			});
		},
		submitOk(){
			if(this.jjnx==0||this.jxyf==0){
				uni.showToast({
					icon: 'none',
					title: '至少评星一项',
					duration: 2000
				});
			}else{
				uni.request({
					url: this.$api+'/weixin/shishenxin/saveassessmentliessoninfo',
					data: this.getmd5({
						lessonId:this.id,
						jjnx:this.jjnx,
						jxyf:this.jxyf,
						reviewReplyContent:this.content
					}),
					header: {
						'content-type': 'application/json'
					},
					method: "POST",
					success: (res) => {
						if (res.data.code != 200) {
							uni.showToast({
							icon: 'none',
							title: res.data.msg,
							duration: 2000
							});
						} else {
							var data = res.data.data;
							uni.showToast(
								{
									title: '评价成功',
									duration: 2000,
									success:()=>{
										uni.navigateBack({
											delta:1
										})
									}
								}
							);
							
						}
					},
					fail: () => {
						uni.hideLoading();
						uni.showToast({
						icon: 'none',
						title: '获取失败',
						duration: 2000
						});
					}
				});
			}
		}
	}
}
